<template>
  <div class="container">
    <div>
      <el-form :inline="true" :model="searchForm" size="mini">
        <el-form-item label="日期">
<!--          <el-date-picker v-model="searchForm.date"-->
<!--                          type="date"-->
<!--                          size="mini"-->
<!--                          placeholder="选择日期"-->
<!--                          value-format="yyyy-MM-dd"-->
<!--                          :picker-options="pickerOptions"-->
<!--          >-->
<!--          </el-date-picker>-->
          <el-date-picker
              v-model="searchForm.daterange"
              type="datetimerange"
              format="yyyy-MM-dd HH:mm"
              value-format="timestamp"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="时间间隔">
          <el-select v-model="searchForm.interval">
            <el-option label="1分钟" :value="1"></el-option>
            <el-option label="5分钟" :value="5"></el-option>
            <el-option label="10分钟" :value="10"></el-option>
            <el-option label="30分钟" :value="30"></el-option>
            <el-option label="1小时" :value="60"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="btn-warning" @click="exportExcel">导出</el-button>
        </el-form-item>
      </el-form>

      <span v-if="searchForm.date">
        当日涨幅：
        namtan: {{ filterListDats.length > 1 ? filterListDats[filterListDats.length-1].data.namtan.v - filterListDats[0].data.namtan.v : '-'}}
        film: {{ filterListDats.length > 1 ? filterListDats[filterListDats.length-1].data.film.v - filterListDats[0].data.film.v : '-'}}
      </span>
      <span >
        涨幅：
        namtan: {{ filterListDats.length > 1 ? filterListDats[filterListDats.length-1].data.namtan.v - filterListDats[0].data.namtan.v : '-'}}
        film: {{ filterListDats.length > 1 ? filterListDats[filterListDats.length-1].data.film.v - filterListDats[0].data.film.v : '-'}}
      </span>
    </div>
    <div id="chart"></div>
    <div id="chart2"></div>
  </div>
</template>
<script>
import axios from "axios";
import * as echarts from 'echarts';
import { exportExcelWithStyle } from './xlsxUtils'

export default {
  data(){
    return {
      searchForm: {
        daterange: [new Date(`${this.parseTime(Date.now(),"{y}-{m}-{d}")} 00:00`).getTime(),new Date(`${this.parseTime(Date.now(),"{y}-{m}-{d}")} 23:59`).getTime()],
        // date: this.parseTime(Date.now(),"{y}-{m}-{d}"),
        interval: 1
      },
      listData: [],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < new Date('2025-06-24').getTime() || time.getTime() > Date.now()
        },
      },
      timerId: null
    }
  },
  created() {
    let data = localStorage.getItem('listData')
    if(data){
      this.listData = JSON.parse(data)
    }
  },
  mounted() {
    console.log(process.env.NODE_ENV)
    this.getList1()
    setInterval(this.getList1, 60 * 1000)
    // this.getList()
    // setInterval(this.getList, 60 * 1000)
    this.initChart()
    this.initChart2()
  },
  computed: {
    filterListDats(){
      let list = []
      if(this.searchForm.date){
        list = this.listData.filter(item => {
          return this.parseTime(parseInt(item.ts),"{y}-{m}-{d}") == this.searchForm.date
        })
      }else if(this.searchForm.daterange && this.searchForm.daterange.length == 2){
        list = this.listData.filter(item => {
          let start = this.searchForm.daterange[0] / 1000
          let end = this.searchForm.daterange[1] / 1000
          console.log(start, end)
          return item.ts >= start && item.ts <= end
        })
      }else{
        list = this.listData
      }
      if(this.searchForm.interval){
        list = list.filter((item,index) => {
          return index % this.searchForm.interval == 0
        })
      }

      return list
    }
  },
  watch: {
    "searchForm.date"(){
      this.initChart()
      this.initChart2()
    },
    "searchForm.daterange"(){
      this.initChart()
      this.initChart2()
    },
    "searchForm.interval"(){
      this.initChart()
      this.initChart2()
    }
  },
  methods: {
    getList1(ts){
      let date = new Date()
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      let h = date.getHours();
      let i = date.getMinutes();

      let that = this

      let _ts = ts ? ts : new Date(`${y}-${m}-${d} ${h}:${i}`).getTime() / 1000 - 60 + ''
      axios.get(`${process.env.NODE_ENV=='development'?'/api':'https://icpolls.com'}/stats?uid=01JXWFG20RKH9Q92VPQZ4KCZP0&ts=${_ts}`,{
        timeout: 10000,
      }).then(res => {
        if(!res.data){
          console.log(this.timerId)
          if(!this.timerId){
            that.timerId = setTimeout(function(){
              that.getList1(_ts)
              // 清空定时器
              clearTimeout(that.timerId) // 不能用clearTimeout清空 clearTimeout只是停止了定时器，但timer的赋值不变
              that.timerId = null
            },5000)
          }
        }else{
          this.setData(res)
        }

      }).catch(err => {
        console.log(err)
        if(!this.timerId){
          let that = this
          that.timerId = setTimeout(function(){
            that.getList1(_ts)
            // 清空定时器
            // clearTimeout(timer) 不能用clearTimeout清空 clearTimeout只是停止了定时器，但timer的赋值不变
            that.timerId = null
          },5000)
        }
      })
    },
    getList(ts){
      let date = new Date()
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      let h = date.getHours();
      let i = date.getMinutes();


      let _ts = ts ? ts : new Date(`${y}-${m}-${d} ${h}:${i}`).getTime() / 1000 - 60 + ''
      axios.get(`/api/stats?uid=01JXWFG20RKH9Q92VPQZ4KCZP0&ts=${_ts}`,{
        timeout: 10000,
      }).then(res => {
        if(!res.data){
          this.getList(_ts)
        }else{
          this.setData(res)
        }

      }).catch(err => {
        console.log(err)
        this.getList()
      })
    },
    setData(res){
      if(res && res.data){
        console.log(res)
        if(this.listData.length > 0 && this.listData[this.listData.length - 1].ts == res.data.ts){
          return
        }
        this.listData.push({
          ts: res.data.ts,
          data: {
            namtan: res.data.c['01JXWFG239EKRFPC1RVE5NG3A0'],
            film: res.data.c['01JXWFG2397M9TVFHPYMQZ0W65']
          }
        })
      }
      localStorage.setItem('listData',JSON.stringify(this.listData))
      this.initChart()
      this.initChart2()
    },
    parseTime(time, cFormat) {
      if (arguments.length === 0 || !time) {
        return null;
      }
      const format = cFormat || "{y}-{m}-{d} {h}:{i}:{s}";
      let date;
      if (typeof time === "object") {
        date = time;
      } else {
        if (typeof time === "string") {
          if (/^[0-9]+$/.test(time)) {
            // support "1548221490638"
            time = parseInt(time);
          } else {
            // support safari
            // https://stackoverflow.com/questions/4310953/invalid-date-in-safari
            time = time.replace(new RegExp(/-/gm), "/");
          }
        }

        if (typeof time === "number" && time.toString().length === 10) {
          time = time * 1000;
        }
        date = new Date(time);
      }
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay(),
      };
      const time_str = format.replace(/{([ymdhisa])+}/g, (result, key) => {
        const value = formatObj[key];
        // Note: getDay() returns 0 on Sunday
        if (key === "a") {
          return ["日", "一", "二", "三", "四", "五", "六"][value];
        }
        return value.toString().padStart(2, "0");
      });
      return time_str;
    },

    initChart(){
      let xArray = this.filterListDats.map(item => {
        return this.parseTime(parseInt(item.ts),"{y}-{m}-{d} {h}:{i}")
      })
      let namtanValues = this.filterListDats.map(item => {
        return item.data.namtan.v
      })
      let filmValues = this.filterListDats.map(item => {
        return item.data.film.v
      })
      let myChart = echarts.init(document.getElementById('chart'));
      // 绘制图表
      myChart.setOption({
        title: {
          text: '冠军秀趋势图'
        },
        dataZoom: [
          {
            id: 'dataZoomX',
            type: 'slider',
            xAxisIndex: [0],
            filterMode: 'filter'
          },

        ],
        tooltip: {
          trigger: 'axis',
          formatter: (params) => {
            return `
            <span>日期: ${params[0].name}</span><br>
            <span>namtan: ${params[0].value}</span><br>
            <span>namtan涨幅: ${params[0].dataIndex > 0 ? params[0].value - namtanValues[params[0].dataIndex - 1] : '-'}</span><br>
            <span>film: ${params[1].value}</span><br>
            <span>film涨幅: ${params[1].dataIndex > 0 ? params[1].value - filmValues[params[1].dataIndex - 1] : '-'}</span><br>
            <span>差值: ${params[0].value - params[1].value}</span><br>
            `
          }
        },
        legend: {
          show: true
        },
        xAxis: {
          type: 'category',
          data: xArray
        },
        yAxis: {
          type: 'value',
          min: 20000
        },
        series: [
          {
            name: 'namtan',
            data: namtanValues,
            type: 'line',
            showSymbol: false,
            smooth: true
          },
          {
            name: 'film',
            data: filmValues,
            type: 'line',
            showSymbol: false,
            smooth: true
          }
        ],
        dataset: {
        }
      });
    },
    initChart2(){
      let xArray = this.filterListDats.map(item => {
        return this.parseTime(parseInt(item.ts),"{y}-{m}-{d} {h}:{i}")
      })
      let namtanValues = this.filterListDats.map((item,index) => {
        return index==0 ? 0 : item.data.namtan.v - this.filterListDats[index-1].data.namtan.v
      })
      console.log(namtanValues)
      let filmValues = this.filterListDats.map((item,index) => {
        return index==0 ? 0 : item.data.film.v - this.filterListDats[index-1].data.film.v
      })
      let myChart = echarts.init(document.getElementById('chart2'));
      // 绘制图表
      myChart.setOption({
        title: {
          text: '涨幅趋势图'
        },
        dataZoom: [
          {
            id: 'dataZoomX',
            type: 'slider',
            xAxisIndex: [0],
            filterMode: 'filter'
          },

        ],
        tooltip: {
          trigger: 'axis',
          // formatter: (params) => {
          //   return `
          //   <span>日期: ${params[0].name}</span><br>
          //   <span>namtan: ${params[0].value}</span><br>
          //   <span>namtan涨幅: ${params[0].dataIndex > 0 ? params[0].value - namtanValues[params[0].dataIndex - 1] : '-'}</span><br>
          //   <span>film: ${params[1].value}</span><br>
          //   <span>film涨幅: ${params[1].dataIndex > 0 ? params[1].value - filmValues[params[1].dataIndex - 1] : '-'}</span><br>
          //   <span>差值: ${params[0].value - params[1].value}</span><br>
          //   `
          // }
        },
        legend: {
          show: true
        },
        xAxis: {
          type: 'category',
          data: xArray
        },
        yAxis: {
          type: 'value',
        },
        series: [
          {
            name: 'namtan',
            data: namtanValues,
            type: 'line',
            showSymbol: false,
            smooth: true
          },
          {
            name: 'film',
            data: filmValues,
            type: 'line',
            showSymbol: false,
            smooth: true
          }
        ],
        dataset: {
        }
      });
    },
    // 导出带有样式的excel
    async exportExcel() {
      var dataArray = []
      var arrayData = []
      var totalArray = []
      var cols = []
      console.log(this.filterListDats)
      // let exportDatas = []
      // if(this.filterListDats.length > 1){
      //   this.filterListDats.forEach((item,index) => {
      //     if(index > 0){
      //       exportDatas.push([
      //         this.parseTime(parseInt(item.ts),"{y}-{m}-{d} {h}:{i}"),
      //       ])
      //     }
      //   })
      // }
      dataArray = this.filterListDats.map((item,index) => {
        if(index==0){
          return [
            this.parseTime(parseInt(item.ts),"{y}-{m}-{d} {h}:{i}"),
            item.data.namtan.v,
            '-',
            item.data.film.v,
            '-'
          ]
        }else{
          return [
            this.parseTime(parseInt(item.ts),"{y}-{m}-{d} {h}:{i}"),
            item.data.namtan.v,
            item.data.namtan.v - this.filterListDats[index-1].data.namtan.v,
            item.data.film.v,
            item.data.film.v - this.filterListDats[index-1].data.film.v
          ]
        }
      })
      arrayData = [['时间','namtan','涨幅','film','涨幅'],
        ...dataArray]
      cols = [200,200,200,200,200]
      const namtanTotalChange = this.filterListDats[this.filterListDats.length - 1].data.namtan.v - this.filterListDats[0].data.namtan.v
      const filmTotalChange = this.filterListDats[this.filterListDats.length - 1].data.film.v - this.filterListDats[0].data.film.v
      totalArray = ['当日总涨幅','',namtanTotalChange,'',filmTotalChange]
      const headerStyle = { background: '#f0f1f6', color: '#33333' }
      let date = this.parseTime(this.filterListDats[this.filterListDats.length-1].ts,'{y}-{m}-{d}')
      exportExcelWithStyle(arrayData, totalArray, `趋势图-${date}`, 'xlsx', {
        cols,
        headerStyle,
      })
    },
  }
}
</script>
<style scoped>
#chart, #chart2 {
  width: 800px;
  height: 400px;
  margin: 10px auto;
}
</style>